import React, { Component } from 'react';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, Alert } from 'reactstrap';

class Forgot extends Component {
  constructor(props) {
    super(props);
    this.updateInputValue = this.updateInputValue.bind(this);
    this.requestForgot = this.requestForgot.bind(this);
    this.state = {
        email: ''
    };
  }

  updateInputValue(evt,val){
    if(val === 1){
      this.setState({
        email: evt.target.value
      }, () => {
        console.log("Email:", this.state.email);
      });
    }
  }

  requestForgot(){
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({ email: this.state.email })
    };
    fetch('https://missionaustralia-back.herokuapp.com/forgot', requestOptions)
    .then(response =>  response.json())
    .then(resData => {
       console.log(resData)
       alert(resData.data);
    });
  }

  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <h1 style={{ textAlign: 'center' }}>Forgot Password</h1>
                      <p className="text-muted">Send password reset link</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            @
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Email" autoComplete="email" id="email" value={this.state.username}  onChange={ evt => this.updateInputValue(evt,1) }/>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4" type="button" onClick={this.requestForgot}>Send</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Forgot;